import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import OverviewMap from '@/components/OverviewMap'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/overviewMap',
      name: 'overviewMap',
      component: OverviewMap
    }
  ]
})



